.AddToDoButton {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 4vw;
  bottom: 4vh;
  border-radius: 50%;
  border: none;
  width: 50px;
  height: 50px;
  background-color: rgb(255, 112, 59);
  color: white;
  transform: rotate(0);
  transition: 0.3s ease;
  z-index: 1;
}
.AddToDoButton:hover {
  background-color: coral;
  transform: rotate(224deg);
  cursor: pointer;
}
.AddToDoButton > svg {
  width: 1.5rem;
  height: 1.5rem;
  color: white;
}
